import React from "react";
import styled from "styled-components";
import { Scene1 } from "./components/scenes/Scene1";
import { Scene2 } from "./components/scenes/Scene2";
import { withRouter, Switch, Route } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";

function App({ location, className }) {
  console.log(location);
  return (
    <div className={`App ${className}`}>
      <TransitionGroup>
        <CSSTransition
          key={location.key}
          classNames="route-swipes"
          timeout={4500}
          unmountOnExit={true}
        >
          <div className="swipe-container">
            <Switch>
              <Route path="/scene2">
                <Scene2></Scene2>
              </Route>
              <Route path="/">
                <Scene1></Scene1>
              </Route>
            </Switch>
          </div>
        </CSSTransition>
      </TransitionGroup>
    </div>
  );
}

export default withRouter(styled(App)`
  /* transition: 1.5s; */
  background-color: black;
  .route-swipes-enter {
    /* transform: translateX(100%); */
    /* .overlay {
      background-color: rgba(1, 1, 1, 1);
    } */
    opacity: 0;
  }
  .route-swipes-enter-active {
    /* transform: translateX(0%); */
    opacity: 1;
    transition: all 4.5s;
  }
  .route-swipes-exit {
    /* transform: translateX(0%); */
    opacity: 1;

    /* transition: 1.5s; */
  }
  .route-swipes-exit-active {
    opacity: 1;
    transition: all 0.5s;
    /* transition: 1.5s; */
  }
`);
