import * as actionTypes from "../_actions/types";

const initialState = {
  authorized: false,
  loading: false,
  error: null,
  role: null
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    // lists
    case actionTypes.LOGIN_REQ:
      return {
        ...state,
        loading: true
      };
    case actionTypes.LOGIN_SUCCESS:
      return {
        error: null,
        authorized: true,
        loading: false,
        role: action.payload.role || null
      };
    case actionTypes.LOGIN_ERROR:
      return {
        error: action.payload,
        authorized: false,
        loading: false,
        role: null
      };
    case actionTypes.LOGOUT:
      return {
        error: false,
        authorized: false,
        loading: false,
        role: null
      };
    default:
      return state;
  }
};
