import styled, { css } from "styled-components";
import React from "react";
import { Link } from "react-router-dom";
import menuSound from "../../../sounds/menu.mp3";
import menuSoundClick from "../../../sounds/menu-click-2.mp3";
import UIfx from "uifx";

const menuHover = new UIfx(menuSound, {
  volume: 0.2, // number between 0.0 ~ 1.0
  throttleMs: 100
});
const menuClick = new UIfx(menuSoundClick, {
  volume: 0.2, // number between 0.0 ~ 1.0
  throttleMs: 100
});

export const NavLink = styled(({ className, children, disabled, to }) => (
  <Link
    to={to}
    className={className}
    onMouseEnter={() => disabled || menuHover.play()}
    onClick={() => disabled || menuClick.play()}
  >
    {children}
  </Link>
))`
  font-size: 4rem;
  color: white;
  text-transform: uppercase;
  letter-spacing: 5px;
  border-bottom: 4px solid transparent;
  border-top: 4px solid transparent;
  cursor: pointer;

  &:hover {
    border-bottom: 4px solid white;
  }
  ${props =>
    props.disabled &&
    css`
      &:hover {
        border-bottom: 4px solid transparent;
        cursor: default;
      }
      color: #999;
    `}
`;
