import React, { useRef, useEffect, useState } from "react";
import { Scene } from "../ui/modules";
import bg from "../../img/bg1.jpg";
import Blur from "react-blur";
import Parallax from "parallax-js";
import { Container, NavLink } from "../ui/elements";
import stalker from "../../sounds/scene-2/stalker.mp3";
import UIfx from "uifx";

const stalkerSound = new UIfx(stalker, {
  volume: 0.3, // number between 0.0 ~ 1.0
  throttleMs: 100
});

export const Scene1 = () => {
  const wrapperEl = useRef(null);

  useEffect(() => {
    const parallax = new Parallax(wrapperEl.current, {});
    stalkerSound.play();
    return () => {
      parallax.disable();
    };
  }, []);

  return (
    <Scene>
      <div className="parallax-wrapper" ref={wrapperEl}>
        <Blur
          img={bg}
          blurRadius={0}
          enableStyles
          className="blur"
          data-depth="0.10"
        ></Blur>

        <div className="overlay background grain"></div>
      </div>
      <div className="content">
        <Container align="center">
          <h1>Опус пернатого</h1>
          <nav>
            <NavLink href="4" to="/scene2">
              Начать историю
            </NavLink>
            <NavLink disabled>Загрузить</NavLink>
          </nav>
        </Container>
      </div>
    </Scene>
  );
};
